body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


header{
    flex: 0 1;
    display:flex;
    background:#171738;
    color: white;
    padding:5px;
    justify-content: center;
    align-items: center;
    font-size:20px;
}

.CalendarContainer{
    display:flex;
    flex-direction: column;
}

header div{
    flex: 0 1 50%;
    text-align: center;
}

header button {
    color: white;
    font-size:20px;
    background:#171738;
    border: 0;
}

.CalendarContainer section{
    margin: 10px auto;
    padding: 0px 10px;
    max-width: 500px;
}
